<template>
    <div>
        <van-nav-bar
                left-text="返回"
                left-arrow
                @click-left="onClickLeft"
        />
        <van-cell title="还没有账号？"><span class="register_text" @click="clickregister">点击注册</span></van-cell>
        <van-form @submit="onSubmit">
            <van-field
                    v-model="email"
                    name="邮箱"
                    label="邮箱"
                    placeholder="邮箱"
                    :rules="[{ required: true, message: '请填写邮箱' }]"
            />

            <van-field
                    v-model="password"
                    type="password"
                    name="密码"
                    label="密码"
                    placeholder="密码"
                    :rules="[{ required: true, message: '请填写密码' }]"
            />
            <div style="margin: 16px;">
                <van-button round block type="info" native-type="submit">
                    提交
                </van-button>
            </div>
        </van-form>
    </div>
</template>

<script>
    export default {
        name: "Login",
        data(){
            return{
                password:'',
                email:''
            }
        },
        created() {
            this.email=this.$getCookie('email')
        },
        methods:{
            onSubmit(){
                const params={
                    'key':'vv9544',
                    'email':this.email,
                    'password':this.password
                }
                this.$axios.post('https://zhuzuoji.com/myapi/employee/login',this.$qs.stringify(params))
                    .then(res=>{
                        // console.log(res)
                        if(res.status==200&&res.data.status){
                            this.$setCookie('username',res.data.info.emp_name,1)
                            this.$setCookie('integral',res.data.info.emp_inte,1)
                            this.$setCookie('tx_img',res.data.info.emp_avatar,1)
                            this.$setCookie('email',res.data.info.emp_email,1)
                            this.$toast.success(res.data.msg)
                            this.$router.push('/person_file')
                        }else {
                            this.$toast.fail('请检查网络')
                        }
                    }).catch(err=>{
                        let error=err.toString()
                        if(error.indexOf('Network')!=-1){
                            this.$toast.fail('请检查网络')
                        }
                    })
            },
            onClickLeft() {
                this.$router.push({path:'/',query:{active:2}})
            },
            clickregister(){
                this.$router.push({path:'/register'})
            },

        }
    }
</script>

<style scoped>
.register_text{
    width: 24px;
    color: crimson;
    font-size: 12px;
}
</style>